import React, { Suspense } from 'react';
import { useSelector } from 'react-redux';
import { ShimmerCardText } from '../../../../stylesheet/Loader';

const InvestorCategoryTypes = React.lazy(() => import('./InvestorCategoryTypes'));

const InvestorCategory = ({ onFormChange}) => {
  const secH = useSelector((store) => store.secH);
  const invFormStatus = useSelector((store) => store.invFormStatus)

  const onChangeInvstrData = ({name, value, investorId}) => {
    onFormChange({name, value, level:"category_details", investorId });
  }

  const renderInvestors = () => {
    return secH?.category_details?.map((each, index)=>{
      const invNum = index+1 
      // const tempId = each?.investor_id || invNum
      const tempId = each?.id || invNum
      if(index < secH?.no_of_investors ){ //phase1 fix. remove this condition in phase-2
        return <InvestorCategoryTypes key={tempId} investorData={each} invId={tempId} investorIndex={invNum} onChangeInvstrData={onChangeInvstrData} itemIndex={index}/>
      }
      return null
    })
  }

  return (
    <div className="section__container--box u-margin-bottom-0">
    {/* <div className="section__container--box"> */}
      <Suspense fallback={<ShimmerCardText />}>
        { secH?.category_details?.length?
          <>
            <h4 className="section__container--subtitle">Investor Category</h4>
            {renderInvestors()}
          </> : null
        }
        
      </Suspense>
    {/* </div> */}
  </div>
  )
}

export default InvestorCategory;